.cmp-adaptiveform-button {
  border: none;
  margin: 12px 0;
  position: relative;
}

.cmp-adaptiveform-button button {
  background-color: #0000000a;
  border-radius: 500px;
}

.cmp-adaptiveform-button button:hover {
  background: #0000001f;
}

.cmp-adaptiveform-button button:active {
  background-color: #00000094;
}

.cmp-adaptiveform-button button[disabled] {
  opacity: .6;
  background: #0000001f;
}

.cmp-adaptiveform-button__widget {
  letter-spacing: 0;
  text-align: center;
  height: 48px;
  cursor: pointer;
  color: #000;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: #248d74;
  border: none;
  margin: 12px 0;
  padding: 0 24px;
  font-family: SourceSansSemiBold, SourceSans, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 17px;
  font-weight: 600;
  line-height: 22px;
}

.cmp-adaptiveform-button__widget:not([disabled]):active {
  color: #fff;
}

.cmp-adaptiveform-button__widget:hover {
  background-color: #186a57;
}

.cmp-adaptiveform-button__widget:disabled {
  cursor: default;
  background-color: #0000000a;
}

@media (max-width: 640px) {
  .cmp-adaptiveform-button__widget {
    width: 100%;
  }
}

.cmp-adaptiveform-button__widget[data-cmp-enabled]:not([data-cmp-enabled="true"]) {
  color: #ddd;
  background: #000000b2;
}

.cmp-adaptiveform-button__longdescription {
  color: #000;
  color: #000000b2;
  background: linear-gradient(156.52deg, #fff6 2.12%, #fff0 39% 54.33%, #ffffff1a 93.02%), #fff linear-gradient(0deg, #ffffff85, #ffffff85);
  border: 2px solid #d5e9e4;
  border-radius: 10px;
  align-items: flex-start;
  gap: 6px;
  margin: 6px 0 12px;
  padding: 12px;
  font-size: .75rem;
  display: flex;
  box-shadow: 0 8px 16px #00000005;
}

.cmp-adaptiveform-button__longdescription:before {
  content: "";
  -webkit-padding-end: 15px;
  width: 16px;
  height: 16px;
  background: url("resources/images/info.svg") no-repeat;
  padding-inline-end: 15px;
}

.cmp-adaptiveform-button__longdescription p {
  margin: 0;
}

.cmp-adaptiveform-button__shortdescription {
  color: #000000b2;
  letter-spacing: 0;
  text-align: left;
  margin: 0;
  font-size: .75rem;
  font-weight: 400;
  line-height: 14px;
  display: flex;
  position: static;
}

.cmp-adaptiveform-button__shortdescription p {
  margin: 0;
}

.cmp-adaptiveform-button__questionmark {
  top: 0;
  width: 16px;
  height: 16px;
  cursor: pointer;
  background: url("resources/images/question.svg") center / cover no-repeat;
  border: none;
  padding: 0;
  display: flex;
  position: absolute;
  inset-inline-end: 0;
}

.cmp-adaptiveform-button__label-container {
  justify-content: space-between;
  display: flex;
}

.cmp-adaptiveform-button__label-container > button[class*="__questionmark"] {
  position: unset;
  inset-inline-end: unset;
}

